  margin-left: 25px;
}

div.constraint-modeler {

  /deep/ .navbar .btn,
  /deep/ .navbar .btn-group .btn {
    padding: 1px 4px;
  }

  /deep/ .nav-link {
    padding-top: 0;
    padding-bottom: 0;
  }

  /deep/ div.navbar {
    border-radius: 7px;
  }

  /deep/ input[type='text'] {
    width: 100px;
  }

  /deep/ .buttons {
    text-align: center;
  }

}

ul.constraintModelerDebug {
  a {
    text-decoration: #0000cc;
    text-decoration-line: underline;
    cursor: pointer;
  }
}

