


























































#app {
  /*font-family: 'Avenir', Helvetica, Arial, sans-serif;*/
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: #2c3e50;
}

nav {
  a {
    &.router-link-exact-active {
      color: #42b983;
    }
  }
}

body {
  overflow-y: scroll;
}

.constraint-modeler {
  border-radius: 7px;
  font-size: 0.9em;
  padding: 10px;
  margin: 0 0 15px;
  background-color: #EEE;
  width: fit-content;
  height: fit-content;
  min-width: 400px;

  .title {
    color: #444;
    border-radius: 7px;
    background-color: #ccc;
    font-size: 1.2em;
    font-weight: bold;
    padding: 5px 0 6px;
    margin: 0 0 5px;
    text-align: center;
  }

}
